<template>
  <h1>{{ $t("currentBooks") }}</h1>
  <input
    class="search-box"
    v-model="searchtoken"
    type="text"
    :placeholder="[[$t('search')]]"
  />
  <div class="container">
    <div class="item" v-for="book in filteredBooks" :key="book.ID">
      <div class="img">
        <router-link :to="`/BooksDetails/${book._id}`" class="nav-link nav-link-body">
          <img :src="'/bookImages/' + book.img || ''" />
        </router-link>
      </div>
      <div class="title">
        <router-link
          :to="`/BooksDetails/${book._id || ''}`"
          class="nav-link nav-link-body"
        >
          <h5>{{ book.Name || "" }}</h5>
          <h6>{{ book.Author || "" }}</h6>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent } from "vue";

import BooksService from "../services/BooksService";

export default defineComponent({
  name: "BooksComponent",
  data() {
    return {
      searchtoken: "",
      booksJson: [],
    };
  },
  async created() {
    try {
      this.booksJson = await BooksService.getAllBooks();
    } catch (err) {
      this.error = err.message;
      console.error(err);
    }
  },
  computed: {
    filteredBooks() {
      return this.booksJson.filter((item) => {
        if (item.Name.toLowerCase().includes(this.searchtoken.toLowerCase())) return true;
        return false;
      });
    },
  },
});
</script>

<style scoped>
.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.item {
  flex-grow: 0;
  flex-shrink: 0;
  flex: 1 0 21%; /* explanation below */
  text-align: center;
  padding-top: 15px;
}

.item + .item {
  margin-left: 2%;
}

.img img {
  height: 150px;
}

.title a {
  color: #000;
  text-decoration: none;
}
.search-box {
  padding: 10px;
  border-radius: 5px;
}
.nav-link-body {
  color: #000 !important;
}
</style>
