<template>
  <navBar />
  <shoppingCartElement
    v-if="this.$route.name !== 'ShoppingCartPage' && this.$route.name !== 'Payment'"
  />
  <div class="container">
    <div class="page-content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import navBar from "./components/navBar.vue";
import shoppingCartElement from "./components/ShoppingCartComponent.vue";

export default {
  name: "App",
  components: {
    navBar,
    shoppingCartElement,
  },
  mounted() {
    this.setLanguage();
  },
  methods: {
    setLanguage() {
      let langInStorage = JSON.parse(window.localStorage.getItem("i18n")) || "en";
      this.$i18n.locale = langInStorage;
    },
  },
};
</script>

