import axios from 'axios';

const url = '/api/books/';


class BooksService {
    static getAllBooks() {
        
        return new Promise((resolve, reject) => {
            axios.get(url).then(function(res) {
                    resolve(res.data.filter((book) => {
                        return book.sold !== true
                      }));
                }).catch(function (error) {
                    reject(error)
                })
        })

        
    }

    static postNewBook(bookObject) {
        return new Promise((resolve, reject) => {
            try {
                axios.post(url,bookObject).then(function(res) {
                    resolve(res.data)
                });
            } catch(err) {
                reject (err);
            }
        })
    }

    static uploadBookPhoto(extension,imageBase64, bookID) {
        return new Promise((resolve, reject) => {
            try {
                axios.post(url + "uploadBookPhoto",{
                    "id":bookID,
                    fileName: bookID + extension,
                    imageBase64
                }).then(function(res) {
                    resolve(res.data)
                });
            } catch(err) {
                reject (err);
            }
        })
    }

    static getBooksDetails(bookID) {
        
        return new Promise((resolve, reject) => {
            axios.get(url + bookID).then(function(res) {
                    const resData = res.data;
                    resolve(resData);
                }).catch(function (error) {
                    reject(error)
                })
        })

        
    }

    static addBookToCart(bookID,bookName,emitter) {
        let shoppingCart = [];
        if(localStorage.getItem("shoppingCart") !== null) {
            shoppingCart = JSON.parse(localStorage.getItem("shoppingCart"));
        }

        if(!this.isBookInCart(bookID)) {
            shoppingCart.push({"bookID":bookID,"bookName":bookName});
            window.localStorage.setItem("shoppingCart", JSON.stringify(shoppingCart));
            emitter.emit("bookAddRemoveFromCart");
            return true;
        }
        return false;
    }

    static removeBookFromCart(bookID,emitter) {
        let shoppingCart = [];
        if(localStorage.getItem("shoppingCart") !== null) {
            shoppingCart = JSON.parse(localStorage.getItem("shoppingCart"));
        }

        shoppingCart = shoppingCart.filter(function(element) { return element.bookID != bookID; }); 
        window.localStorage.setItem("shoppingCart", JSON.stringify(shoppingCart));
        emitter.emit("bookAddRemoveFromCart");
        return true;
    }

    
    static emptyCart() {
        window.localStorage.removeItem("shoppingCart");
        return true;
    }

    static isBookInCart(bookID) {
        let book = [];
        if(localStorage.getItem("shoppingCart") !== null) {
            let shoppingCart = JSON.parse(localStorage.getItem("shoppingCart"));
            book = shoppingCart.filter(function(element) { return element.bookID === bookID; }); 
        }

        return book.length > 0;
    }

    static getShoppingCartItems() {
        let shoppingCart = [];
        if(localStorage.getItem("shoppingCart") !== null) {
            shoppingCart = JSON.parse(localStorage.getItem("shoppingCart"));
        }
        return shoppingCart;
    }
}

export default BooksService;