
import Home from './views/Home.vue'
import PageNotFound from './views/PageNotFound.vue'

import {createRouter, createWebHistory} from "vue-router";


const routes = [
      {
        path: '/',
        name: 'Home',
        component: Home
      },

      {
        path: '/login',
        name: 'Login',
        component: () => import('./views/Login.vue')
      },
    //   {
    //     path: '/assets/',
    //   },
      {
        path: '/BooksDetails/:bookid',
        name: 'BooksDetails',
        component: () => import('./views/BooksDetails.vue')
      },
      {
        path: '/userProfile',
        name: 'UserProfile',
        component: () => import('./views/UserProfile.vue'),
        meta: { requiresAuth : true }
      },
      {
        path: '/ShoppingCartPage',
        name: 'ShoppingCartPage',
        component: ()=>import('./views/ShoppingCartPage.vue'),
        meta: {requiresAuth:true}
      },
      {
        path: '/Payment',
        name: 'Payment',
        component: ()=>import('./views/Payment.vue'),
        meta: {requiresAuth:true}
      },
      {
        path: '/Orders',
        name: 'Orders',
        component: ()=>import('./views/Orders.vue'),
        meta: {requiresAuth:true}
      },
      {
        path: '/NewBook',
        name: 'NewBook',
        component: ()=>import('./views/NewBook.vue'),
        meta: {requiresAuth:true}
      },
      {
        path: '/BookImage/:id',
        name: 'BookImage',
        component: ()=>import('./views/BookImage.vue'),
        meta: {requiresAuth:true}
      },
      {
        path: '/:catchAll(.*)*',
        name: "PageNotFound",
        component: PageNotFound,
      },
    
];


const router = createRouter({
  history: createWebHistory(),
  routes
});

router.beforeEach((to, from, next) => {
  const requiresAuth = to.matched.some(record => record.meta.requiresAuth);
  if (requiresAuth) {
    if (localStorage.getItem("loggedInUserID") !== null) {
      next();
    } else {
      next("/login");
    }
  } else if (to.name == "Login" && localStorage.getItem("loggedInUserID") !== null) {
    next("/");
  }
  next();
});


