import axios from 'axios';

const url = '/api/users/'


class UsersService {
    static signIn(username,password) {
        return new Promise((resolve, reject) => {
            try {
                let res= axios.post(url + "login",{
                    username,
                    password
                });
                resolve(res)
            } catch(err) {
                reject (err);
            }
        })
    }

    static addLoginToLocalStorage(userID) {
        window.localStorage.setItem("loggedInUserID", JSON.stringify(userID));
    }

    static getUserIDFromLocalStorage(){
        if(localStorage.getItem("loggedInUserID") !== null) {
            return JSON.parse(localStorage.getItem("loggedInUserID"));
        }
        return null;
    }

    static getUserInfo() {
        return new Promise((resolve, reject) => {
            try {
                axios.get(url + this.getUserIDFromLocalStorage()).then(function(res) {
                    const resData = res.data.responseObject[0];
                    resolve(resData);
                }).catch(function (error) {
                    reject(error)
                })
            } catch(err) {
                reject(err);
            }
        })
    }

    static updateUser(userObject) {
        return new Promise((resolve, reject) => {
            try {
                axios.post(url + "updateUserInfo",{
                    "id":this.getUserIDFromLocalStorage(),
                    "userObject" : userObject
                }).then(function(res) {
                    resolve(res.data)
                });
            } catch(err) {
                reject (err);
            }
        })
    }

    static registerUser(username,password,email) {
        return new Promise((resolve, reject) => {
            try {
                axios.post(url + "register",{
                    username,
                    password,
                    "emailAddress": email
                }).then(function(res) {
                    resolve(res.data)
                });
            } catch(err) {
                reject (err);
            }
        })
    }

    static logOut() {
        localStorage.removeItem("loggedInUserID");
      window.location.reload();
    }

    static deleteUser(id) {
        return axios.delete(`${url}${id}`);
    }

    static uploadUserPhoto(extension,imageBase64) {
        return new Promise((resolve, reject) => {
            try {
                axios.post(url + "uploadUserPhoto",{
                    "id":this.getUserIDFromLocalStorage(),
                    fileName: this.getUserIDFromLocalStorage() + extension,
                    imageBase64
                }).then(function(res) {
                    resolve(res.data)
                });
            } catch(err) {
                reject (err);
            }
        })
    }
}

export default UsersService;