<template>
  <a
    v-if="userLoggedIn"
    @click.prevent="toggleShoppingElement()"
    href="#"
    id="salesElement"
    class="visible"
    ><i
      style="font-size: 50px; color: #fff; margin-top: 22px; margin-left: 15px"
      class="fas fa-shopping-cart"
    ></i
  ></a>
  <div v-if="userLoggedIn" id="salesContactDiv" class="hidden">
    <a @click.prevent="toggleShoppingElement()" href="#" id="closeSalesElement"
      ><i class="fas fa-times closeIcon"></i
    ></a>

    <div class="salesContactDiv-inner">
      <div
        style="height: 100px !important"
        class="heroBackground heroBackgroundSmall"
      ></div>
      <h1
        style="height: 100px; font-family:Poppins; padding-top:10px; font-weight:bold: color:#fff;"
      >
        {{ $t("shoppingCart") }}
      </h1>
    </div>
    <ul class="list-group">
      <li v-for="book in shoppingCartArr" :key="book.bookID" class="list-group-item">
        {{ book.bookName }}
        <a href="#" @click.prevent="removeItem(book.bookID)"
          ><i style="padding-left: 10px" class="fas fa-trash-alt"></i
        ></a>
      </li>
    </ul>
    <button
      v-if="shoppingCartArr.length > 0"
      style="position: absolute; bottom: 10px; left: 33%"
      type="button"
      class="btn btn-primary"
      @click.prevent="buyNow()"
    >
      {{ $t("buyNow") }}
    </button>
    <div v-else style="color: rgba(95, 158, 160, 0.6)">
      <i
        style="font-size: 4em; margin-top: 25px; margin-bottom: 30px"
        class="fas fa-book"
      ></i>
      <h3>{{ $t("emptyCart") }}</h3>
    </div>
  </div>
</template>

<script>
import { defineComponent } from "vue";
import BooksService from "../services/BooksService";

export default defineComponent({
  name: "shoppingCartElement",
  data() {
    return {
      userLoggedIn: false,
      expanded: false,
      shoppingCartArr: [],
    };
  },
  mounted: function () {
    this.emitter.on("bookAddRemoveFromCart", () => {
      this.shoppingCartArr = BooksService.getShoppingCartItems();
    });
  },
  emits: ["bookAdded"],
  created() {
    this.userLoggedIn = localStorage.getItem("loggedInUserID") !== null;
    this.shoppingCartArr = BooksService.getShoppingCartItems();
  },
  methods: {
    toggleShoppingElement() {
      let elementCircle = document.getElementById("salesElement");
      let element = document.getElementById("salesContactDiv");
      if (!this.expanded) {
        elementCircle.classList.remove("visible");
        elementCircle.classList.add("hidden");
        element.classList.remove("hidden");
        element.classList.add("visible");
      } else {
        element.classList.remove("visible");
        element.classList.add("hidden");
        elementCircle.classList.remove("hidden");
        elementCircle.classList.add("visible");
      }
      this.expanded = !this.expanded;
    },
    removeItem(bookID) {
      BooksService.removeBookFromCart(bookID, this.emitter);
    },
    buyNow() {
      this.toggleShoppingElement();
      this.$router.push("/ShoppingCartPage");
    },
  },
});
</script>

<style scope>
#salesElement {
  z-index: 9999;
  position: fixed;
  bottom: 40px;
  right: 40px;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  background-color: #5f9ea0;
  background-size: contain;
  background-repeat: no-repeat;
  box-shadow: var(--box-shadow);
  transition: 0.7s;
}

#salesElement:hover {
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.3);
  transition: 0.3s;
}
/* Sales Element Animation*/

@keyframes scaleUp {
  0% {
    transform: scale(0);
  }
  90% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

#salesElement.visible {
  display: block;
  animation: 0.3s ease-in-out 0.25s 1 scaleUp both;
}

@keyframes scaleDown {
  0% {
    transform: scale(1);
  }
  90% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(0);
  }
}

#salesElement.hidden {
  display: block;
  animation: 0.1s ease-in-out 0s 1 scaleDown both;
}

.phoneIcon {
  background-color: #2f3956;
  color: #fff;
  position: absolute;
  padding-top: 10px;
  height: 35px;
  width: 35px;
  top: -4px;
  right: -4px;
  border-radius: 50%;
  text-align: center;
  pointer-events: none;
  box-shadow: var(--box-shadow);
}

@media only screen and (max-width: 768px) {
  #salesElement {
    width: 90px;
    height: 90px;
    bottom: 20px;
    right: 20px;
  }

  .phoneIcon {
    padding-top: 7px;
    height: 30px;
    width: 30px;
  }
}

.closeIcon {
  display: block;
  background-color: #fff;
  margin: -18px -18px -18px 0;
  color: #2f3956;
  position: absolute;
  padding-top: 10px;
  height: 35px;
  width: 35px;
  top: 0;
  right: 0;
  border-radius: 50%;
  text-align: center;
  box-shadow: var(--box-shadow);
}

.salesContactDivWrapper {
  bottom: 40px;
  right: 40px;
}

.salesContactDiv-inner {
  overflow: hidden;
  position: relative;
}

#salesContactDiv {
  z-index: 9999;
  display: none;
  position: fixed;
  bottom: 40px;
  right: 40px;
  width: 310px;
  height: 390px;
  text-align: center;
  box-shadow: var(--box-shadow);
  background-color: #fff;
}

/* sales card animation */

@keyframes slideInFromBottom {
  0% {
    transform: translateY(100%);
  }
  90% {
    transform: translateY(-3%);
  }
  100% {
    transform: translateY(0);
  }
}

#salesContactDiv.visible {
  display: block;
  animation: 0.25s ease-in-out 0s 1 slideInFromBottom;
  border-radius: 10px;
}

@keyframes slideOutBottom {
  0% {
    transform: translateY(0);
  }
  10% {
    transform: translateY(-3%);
  }
  100% {
    transform: translateY(200%);
  }
}

#salesContactDiv.hidden {
  display: block;
  animation: 0.5s ease-in-out 0s 1 slideOutBottom forwards;
}

.heroBackgroundSmall {
  height: 170px !important;
  transform: skewY(-8deg);
  margin-top: -25px;
}
.heroBackground {
  width: 100%;
  height: 700px;
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(90deg, #5f9ea0 0%, #5f9ea0 100%);
  z-index: -1000;
}

.list-group-item {
  border: none !important;
  border-bottom: 1px solid rgba(0, 0, 0, 0.125) !important;
}
</style>
