<template>
  <nav class="navbar navbar-expand-lg navbar-light rounded">
    <router-link to="/"
      ><img class="logo" :src="require('../assets/logo.png')" /><span class="logotext"
        >Weed out school books</span
      ></router-link
    >

    <div class="collapse navbar-collapse contents-right" id="navbarsExample09">
      <ul class="navbar-nav mr-auto">
        <li v-if="!userLoggedIn" class="nav-item active">
          <router-link to="/" class="nav-link">{{ $t("home") }}</router-link>
        </li>
        <li v-if="!userLoggedIn" class="nav-item">
          <router-link to="/login" class="nav-link">{{ $t("login") }}</router-link>
        </li>
        <li v-else>
          <div class="dropdown">
            <button
              class="btn btn-secondary dropdown-toggle"
              type="button"
              id="dropdownMenuButton"
              data-bs-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >
              {{ userInfo.firstName }} {{ userInfo.lastName }}
              <img style="width: 20px" :src="'/userImages/' + userInfo.profilePicture" />
            </button>
            <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
              <router-link to="/userProfile" class="dropdown-item"
                ><i class="fas fa-user"></i> {{ $t("myProfile") }}</router-link
              >
              <router-link to="/newBook" class="dropdown-item">
                <i class="fas fa-money-bill-wave-alt"></i>
                {{ $t("sellABook") }}</router-link
              >
              <router-link to="/Orders" class="dropdown-item"
                ><i class="fas fa-shopping-bag"></i> {{ $t("myOrders") }}</router-link
              >

              <a @click.prevent="logOut()" class="dropdown-item" href="#"
                ><i class="fas fa-sign-out-alt"></i> {{ $t("logOut") }}</a
              >
            </div>
          </div>
        </li>
        <li>
          <div class="dropdown" style="margin-left: 10px">
            <button
              class="btn btn-secondary dropdown-toggle"
              type="button"
              id="dropdownMenuButton"
              data-bs-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
              style="background-color: transparent"
            >
              <img
                style="width: 20px"
                :src="require('../assets/language/languagesIcon.webp')"
              />
            </button>
            <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
              <a @click.prevent="setLanguage('de')" class="dropdown-item" href="#"
                ><img class="logo" :src="require('../assets/language/de.png')"
              /></a>
              <a @click.prevent="setLanguage('en')" class="dropdown-item" href="#"
                ><img class="logo" :src="require('../assets/language/en.png')"
              /></a>
              <a @click.prevent="setLanguage('py')" class="dropdown-item" href="#"
                ><img class="logo" :src="require('../assets/language/py.png')"
              /></a>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>
import { defineComponent } from "vue";
import UsersService from "../services/UsersService";

export default defineComponent({
  name: "navBar",
  data() {
    return {
      userLoggedIn: false,
      userInfo: {},
    };
  },
  async mounted() {
    this.loadLanguage();
    if (localStorage.getItem("loggedInUserID") !== null) {
      this.userLoggedIn = true;
      this.userInfo = await UsersService.getUserInfo();
      this.userInfo.profilePicture = this.userInfo.profilePicture || "user.png";
    }
    if (
      this.userInfo.firstName === "" ||
      this.userInfo.firstName === null ||
      this.userInfo.firstName === undefined
    ) {
      this.$router.push(`userProfile`);
    }
  },
  updated() {
    this.userLoggedIn = localStorage.getItem("loggedInUserID") !== null;
  },
  methods: {
    setLanguage(lang) {
      window.localStorage.setItem("i18n", JSON.stringify(lang));
      let langInStorage = JSON.parse(window.localStorage.getItem("i18n")) || "en";
      this.$i18n.locale = langInStorage;
    },
    loadLanguage() {
      let langInStorage = JSON.parse(window.localStorage.getItem("i18n")) || "en";
      this.$i18n.locale = langInStorage;
    },
    logOut() {
      UsersService.logOut();
    },
  },
});
</script>

<style>
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap");
.contents-right {
  justify-content: right;
  padding-right: 30px;
  column-gap: 2em;
}

.navbar {
  background-color: cadetblue !important;
  margin-bottom: 20px;
  padding-left: 20px;
}

.nav-link {
  color: #fff !important;
}

.active {
  font-weight: bold !important;
}

.logo {
  width: 50px;
}

.logotext {
  font-family: "Poppins", sans-serif;
  margin-left: 15px;
  color: #fff;
  font-size: 1.2em;
  font-weight: bold;
  text-decoration: none;
}
</style>
